<template>
    <div
        v-if="props.myModal.showModal"
        class="modal show d-block"
        tabindex="-1"
        :aria-labelledby="props.myModal.modalId"
        :aria-hidden="!props.myModal.showModal"
        @click="handleBackgroundClick"
        :class="{ 'static-backdrop-animation': animateBackdrop }"
    >
        <div class="modal-dialog" :class="[myModal.size, myModal.scrollable ? 'modal-dialog-scrollable' : '']">
            <div class="modal-content">
                <form @submit.prevent="handleSubmit">
                    <div class="modal-header">
                        <h5 class="modal-title">{{ props.myModal.title }}</h5>
                        <button type="button" class="btn-close" @click="$emit('cancel')"></button>
                    </div>
                    <div class="modal-body">
                        <div class="d-flex mb-2 btn-toolbar justify-content-end mb-3">
                            <div class="btn-group me-2 mb-3" role="group" aria-label="First group">
                                <!-- 新增搜尋輸入框 -->
                                <div class="input-group ms-3" style="max-width: 200px">
                                    <input type="text" class="form-control" placeholder="搜尋" v-model="searchQuery" />
                                    <div
                                        class="btn-close position-absolute end-0 top-50 translate-middle cursor-pointer shadow-none"
                                        @click="clearSearch"
                                        aria-label="清除搜尋"
                                    >
                                        <button class="btn btn-link p-0" aria-label="Close"></button>
                                    </div>
                                </div>
                                <button type="button" class="btn btn-esun-outline" @click="doSearch">搜尋</button>
                                <button
                                    type="button"
                                    class="btn btn-esun-outline"
                                    :class="{ active: displayMode === 'card' }"
                                    @click="displayMode = 'card'"
                                >
                                    卡片顯示
                                </button>
                                <button
                                    type="button"
                                    class="btn btn-esun-outline"
                                    :class="{ active: displayMode === 'table' }"
                                    @click="displayMode = 'table'"
                                >
                                    表格顯示
                                </button>
                            </div>
                        </div>
                        <div v-if="internalSelectedList.length === 0" class="text-muted">無選取項目</div>

                        <div v-if="displayMode === 'card'" class="d-flex flex-wrap w-100">
                            <div
                                v-for="(item, index) in pagedList"
                                :key="index"
                                class="p-2"
                                style="flex: 1 1 0; min-width: 25%; max-width: 25%; width: 25%"
                            >
                                <div class="card">
                                    <div
                                        class="card-body p-2 d-flex flex-column justify-content-between"
                                        style="height: 100px"
                                    >
                                        <div class="mb-2 text-truncate" style="width: 100%">{{ item.name }}</div>
                                        <button
                                            type="button"
                                            :data-is-disabled="props.disabledDataList?.includes(item.value)"
                                            v-if="props.disabledDataList?.includes(item.value) === false"
                                            class="btn btn-sm btn-danger align-self-end"
                                            @click="onSelectInternal(index)"
                                        >
                                            選取
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <table v-if="displayMode === 'table'" class="table table-bordered">
                            <thead>
                                <tr>
                                    <th>項目</th>
                                    <th>操作</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in pagedList" :key="index">
                                    <td>{{ item.name }}</td>
                                    <td>
                                        <button
                                            :data-is-disabled="props.disabledDataList?.includes(item.value)"
                                            v-if="props.disabledDataList?.includes(item.value) === false"
                                            type="button"
                                            class="btn btn-sm btn-danger"
                                            @click="onSelectInternal(index)"
                                        >
                                            選取
                                        </button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>

                        <Pagination
                            v-if="totalItems"
                            v-model:modelValue="pageNumber"
                            v-model:pageSize="pageSize"
                            :total-items="totalItems"
                            :goToPage="onPageChangeInternal"
                        />
                    </div>
                    <div class="modal-footer">
                        <button class="btn btn-esun-outline" @click.prevent="$emit('cancel')">取消</button>
                        <button type="submit" class="btn btn-esun-primary">確定</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</template>

<script lang="ts" setup>
import { ref, watchEffect, computed } from 'vue';
import Pagination from '../../pagination/Pagination.vue';
import type { MySelectListModal, InternalSelectedList } from './schema';

// 定義組件接收的屬性，新增 paginationType 可選參數，表示分頁類型（client 或 server）
const props = defineProps<{
    myModal: MySelectListModal; // 傳入的模態框配置物件
    disabledDataList: string[] | null; // 禁用的項目列表
    selectedList: InternalSelectedList[]; // 已選取的項目列表
    totalItems: number; // 總項目數
    onPageChange?: (searchQuery: string, page: number, pageSize: number) => void; // 分頁變更回調函式
    onSelect: (selected?: any) => void; // 選取項目變更回調函式
    paginationType?: 'client' | 'server'; // 分頁類型，client 端或 server 端
}>();
const emit = defineEmits<{
    (e: 'update:selectedList', payload: any[]): void; // 更新選取列表事件
    (e: 'update:totalItems', payload: number): void; // 更新總項目數事件
    (e: 'submit'): void; // 提交事件
    (e: 'cancel'): void; // 取消事件
}>();

// 控制背景動畫狀態
const animateBackdrop = ref(false);

// 內部維護搜尋
const searchQuery = ref('');

// 內部維護的選取列表，初始為傳入的 selectedList 或空陣列
const internalSelectedListFullData = ref<InternalSelectedList[]>([]);
const internalSelectedList = ref<InternalSelectedList[]>([]);

// 當前頁碼，初始為 1
const pageNumber = ref(1);

// 每頁顯示數量，初始為 10
const pageSize = ref(10);

// 計算總項目數，為總頁數乘以每頁大小
const totalItems = ref<number>(props.totalItems);

// 顯示模式，卡片或表格，預設為卡片
const displayMode = ref<'card' | 'table'>('card');

// 計算當前頁面要顯示的項目列表
const pagedList = computed(() => {
    // 確保使用最新的內部選取列表
    const currentList = internalSelectedList.value || [];

    if (props.paginationType === 'client') {
        // 客戶端分頁，根據當前頁碼和頁大小切割列表
        const start = (pageNumber.value - 1) * pageSize.value;
        const end = start + pageSize.value;

        // 確保不會超出陣列範圍
        if (start >= currentList.length) {
            return [];
        }

        return currentList.slice(start, end);
    } else {
        // 伺服器端分頁，直接返回整個列表
        return currentList;
    }
});

// 監聽傳入的 selectedList 變化，更新內部選取列表
watchEffect(() => {
    const newList = props.selectedList || [];
    internalSelectedListFullData.value = [...newList];
    internalSelectedList.value = [...newList];
});

// 監聽傳入的 totalItems 變化，更新內部總頁數
watchEffect(() => {
    totalItems.value = props.paginationType === 'client' ? internalSelectedList.value.length : props.totalItems;
    // 客戶端分頁 時，直接返回內部選取列表的長度
});

// 選定
function onSelectInternal(index: number) {
    // 如果有 onSelect 回調，呼叫它
    if (props.onSelect) {
        props.onSelect(internalSelectedList.value[index].value);
    }
}

// 處理頁碼變更
function onPageChangeInternal(page: number, newPageSize: number) {
    pageNumber.value = page;
    pageSize.value = newPageSize;
    // 發出更新總頁數事件
    // emit('update:totalPages', internalTotalPages.value); 在外部組件中處理總頁數更新 再 Props totalPages
    // 如果有 onPageChange 回調，呼叫它
    if (props.onPageChange) {
        props.onPageChange(searchQuery.value, page, newPageSize);
    }
}

// 處理搜尋
function doSearch() {
    if (props.paginationType === 'client') {
        internalSelectedList.value;
    } else {
        if (props.onPageChange) {
            props.onPageChange(searchQuery.value, pageNumber.value, pageSize.value);
        }
    }
}

function clearSearch() {}

// 處理點擊背景事件，觸發背景動畫
function handleBackgroundClick(event: MouseEvent) {
    if (event.target === event.currentTarget) {
        animateBackdrop.value = true;
        setTimeout(() => {
            animateBackdrop.value = false;
        }, 150);
    }
}

// 處理表單提交事件，發出 submit 事件
function handleSubmit() {
    emit('submit');
}
</script>

<style scoped>
.modal {
    background-color: rgba(0, 0, 0, 0.5);
}
.modal-dialog {
    background-color: transparent;
}

.static-backdrop-animation {
    animation: staticBackdropAnimation 0.15s ease-in-out;
}

.modal-dialog-scrollable .modal-body {
    max-height: calc(100vh - 200px);
    overflow-y: auto;
}

@keyframes staticBackdropAnimation {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.02);
    }
    100% {
        transform: scale(1);
    }
}
</style>
